import React, { Component } from "react";

import { List, NavBar, Icon, Pagination, Button, Toast } from "antd-mobile";
import { connect } from "react-redux";
const Highcharts = require("highcharts");
const ReactHighcharts = require("react-highcharts"); // Expects that Highcharts was loaded in the code.
const QRCode = require("qrcode.react");

require("highcharts-3d")(ReactHighcharts.Highcharts);
// // Load module after Highcharts is loaded
// require("highcharts/modules/exporting")(Highcharts);
// log(Highcharts);
import "./index.less";
const colorTheme = {
  0: "#e84118",
  1: "#00a8ff",
  2: "#fbc531",
  3: "#4cd137",
  4: "#487eb0",
  5: "#9c88ff"
};
let randomCount = parseInt(Math.random() * 10) % 5;
class index extends Component {
  state = {
    total: 0,
    option: {},
    correctCount: 0,
    errorCount: 0
  };
  componentDidMount() {
    let { teacherQuestion } = this.props;
    let list = teacherQuestion.list;
    let correctList = list.filter(item => {
      if (item.is_error === 0) {
        return true;
      } else {
        return false;
      }
    });
    let correctCount = correctList.length || 0;
    let errorCount = list.length - correctCount;
    this.setState({
      correctCount,
      errorCount
    });
    log(correctCount, errorCount);
    let option = {
      chart: {
        type: "pie",
        height: 220,
        backgroundColor: "transparent",
        options3d: {
          enabled: true,
          alpha: 45,
          beta: 0
        }
      },
      colors: ["#65D139", "#e84118"],
      title: {
        text: null
      },
      tooltip: {
        pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>"
      },
      plotOptions: {
        pie: {
          depth: 35,
          allowPointSelect: true,
          cursor: "pointer",
          dataLabels: {
            enabled: false
          },
          showInLegend: true
        }
      },
      series: [
        {
          type: "pie",
          name: "比例",
          data: [
            { name: "正确", y: correctCount },
            {
              name: "错误",
              y: errorCount,
              sliced: true,
              selected: true
            }
          ]
        }
      ]
    };
    // Highcharts.chart("highcharts", option);
    this.setState({
      option
    });
  }
  gotoLast = () => {
    this.props.history.go(-1);
  };
  render() {
    let { teacherQuestion } = this.props;
    log(teacherQuestion);
    const navBar = (
      <NavBar
        style={{
          backgroundColor: colorTheme[randomCount],
          color: "#fff"
        }}
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        Review and learn
      </NavBar>
    );
    const title = (
      <div className="title">
        <h2>答卷总结</h2>
        <div className="exam">
          {teacherQuestion && teacherQuestion.collection_info.name}
          <p>
            {teacherQuestion && teacherQuestion.collection_info.note}{" "}
            <span>[{teacherQuestion && teacherQuestion.list.length}道题]</span>
          </p>
        </div>
      </div>
    );
    const correctCell = (
      <div className="correct">
        答对了<span>{this.state.correctCount}</span>题，答错了
        <span>{this.state.errorCount}</span>题， 用时
        <span>{this.props.teacherQuestion.duration}</span>秒
      </div>
    );
    const chart = (
      <div id="highcharts" className="chart">
        chart
      </div>
    );
    const hchart = (
      <ReactHighcharts className="chart" config={this.state.option} />
    );

    const qrCode = (
      <div className="qrcode">
        <QRCode
          value={teacherQuestion.exam_address}
          fgColor={colorTheme[randomCount]}
          bgColor="transparent"
          size={100}
        />
        <p>题库地址</p>
      </div>
    );
    const content = (
      <div className="content">
        {title}
        {correctCell}
        {hchart}

        {qrCode}
      </div>
    );
    return (
      <div className="judge-exam">
        {navBar}
        {content}
      </div>
    );
  }
}
const mapStateToProps = state => ({
  teacherQuestion: state.teacherQuestion
});
export default connect(mapStateToProps)(index);
